import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { IonInfiniteScroll, ViewWillEnter } from "@ionic/angular";
import { Observable } from "rxjs";
import { map } from "rxjs/operators";
import { ApiProductSearchResponse, ProductsApiService } from "src/app/api";
import { AuthenticationService, UserInfo } from "src/app/services";

@Component({
  selector: "app-search",
  templateUrl: "./search.page.html",
  styleUrls: ["./search.page.scss"],
})
export class SearchPage implements OnInit, ViewWillEnter {
  private $searchQuery: string;

  public readonly data$: Observable<Array<ApiProductSearchResponse>>;
  public readonly id$: Observable<string>;
  public readonly userInfo$: Observable<UserInfo>;

  public get searchQuery(): string {
    return this.$searchQuery;
  }
  public set searchQuery(value: string) {
    this.$searchQuery = value;
    this.data = [];
    this.noData = false;
    this.loadData();
  }
  public data: ApiProductSearchResponse[] = [];
  public noData: boolean;

  public constructor(
    activatedRoute: ActivatedRoute,
    authenticationService: AuthenticationService,
    private readonly productsApiService: ProductsApiService,
  ) {
    this.id$ = activatedRoute.paramMap.pipe(
      map((p) => (p.get("id") === "search" ? null : p.get("id"))),
    );
    this.userInfo$ = authenticationService.userInfo$;
  }

  public loadData(event?: CustomEvent): void {
    const data = this.data;
    const target = (event?.target as unknown) as IonInfiniteScroll;

    this.productsApiService
      .productsSearchProducts({
        q: this.searchQuery,
        skip: data.length,
        take: 25,
      })
      .subscribe((result) => {
        data.push(...result.items);

        if (this.data === data && this.data.length === 0) {
          this.noData = false;
        }
        if (target) {
          target.disabled = result.end && this.data === data;
          void target.complete();
        }
      });
  }

  public ngOnInit(): void {
    this.ionViewWillEnter();
  }

  public ionViewWillEnter(): void {
    this.searchQuery = "";
  }
}
